@import "vars";
@import "layout";



.product-title {
    font-size: 1.5rem;
    color: #fff;
    text-overflow: ellipsis;
    min-width: 0;
    overflow: hidden;
}

.menu-item {
    color: #fff;
    font-size: 0.9rem;
}

.site-header {
    box-shadow: 2px 4px 12px rgba(0, 0, 0, 0.25); 
    position: fixed; 
    top: 0; 
    left: 0; 
    right: 0; 
    width: 100vw; 
    height: 42px; 
    padding-left: 16px; 
    padding-right: 16px; 
    box-sizing: border-box; 
    background: #444;
    color: #fff;
    z-index: 10;

    a {
        text-decoration: none;
    }
}

.site-footer {
    font-size: 0.8em; 
    width: 100%; 
    height: 42px; 
    background: #444; 
    color: #fff;

    a {
        text-decoration: none;
    }
}

.section {
    @include debugBorder(#cc0);

    .section-header {

        @include debugBorder(orange);

        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: auto;
        grid-template-areas:
            "section-title1"
            "section-title2"
            "section-title3";

        .section-title1 {
            grid-area: section-title1;
            justify-self: center;
            text-align: center;
        }

        .section-title2 {
            grid-area: section-title2;
            justify-self: center;
            min-height: 0;
        }

        .section-title3 {
            grid-area: section-title3;
            justify-self: center;
            min-height: 0;
        }
    }

    .section-header._has-image {
        display: grid;
        grid-template-columns: min-content min-content max-content min-content;
        grid-template-rows: auto;
        grid-template-areas:
            ". section-image section-title1 ."
            ". . section-title2 section-title2"
            ". section-title3 section-title3 .";

        .section-image {
            grid-area: section-image;
            padding-right: 1em;
        }

        .section-title1 {
            grid-area: section-title1;
            justify-self: start;
        }

        .section-title2 {
            grid-area: section-title2;
        }

        .section-title3 {
            grid-area: section-title3;
        }
    }
}


@media (max-width: $ONE_COL_MAX_WIDTH) {

    .section {
        .section-header {
            &._has-image {
                grid-template-columns: 1fr;
                grid-template-areas:
                    "section-image"
                    "section-title1"
                    "section-title2";

                .section-image {
                    padding-right: 0;
                    justify-self: center;
                }

                .section-title1 {
                    justify-self: center;
                    text-align: center;
                }
            }
        }
    }
}

////////////////////////////////////////////////////////////////

.section {
    div {
        position: relative;
    }

    &.pixodesk-section {
        margin-top: 4rem;

        .section-header .section-title1 {
            font-size: 2.5rem;
        }
    }

    .section-header {
        .section-title1 {
            font-size: 2rem;
            line-height: 170%;
            font-weight: 500;
            color: #2b5192;
        }

        .section-title2,
        .section-title3 {
            justify-self: center;

            &,
            & div {
                font-size: 1rem;
                line-height: 170%;
                font-weight: 300;
            }

            & li {
                text-align: justify;
                font-size: 1rem;
                line-height: 170%;
                font-weight: 300;
            }
        }
    }
}

////////////////////////////////////////////////////////////////

._first-col, ._second-col {
    position: relative;
    max-width: 50%;
    min-width: 0;
}
._first-col { 
    @include debugBorder(green);
}
._second-col { 
    @include debugBorder(blue);
}

.section {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    box-sizing: border-box;

    ._columns {
        display: flex;
        flex-direction: row;
        align-items: center;
        align-content: center;
        flex-wrap: wrap;
    }

    // ._first-col { width: 50%; }
    // ._second-col { width: 50%; }

    ._first-col, ._second-col {
        flex-grow: 1;
        min-width: 400px;
        display: flex;
        flex-direction: column;
        align-items: center;
    
        div, img, video {
            align-self: center;
        }

    }

    ._first-col {
        // display: flex;
        // flex-direction: column;

        // img {
        //     align-self: end;
        // }
    }

    .section-header-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        .section-header {
            .section-image { @include debugBorder(#eee); }
            .section-title1 { @include debugBorder(#eee); }
            .section-title2 { @include debugBorder(#eee); }
        }
    }

    .section-content {
        max-width: calc(min(60em, 100%));
    }


    &.pixodesk-section {
        margin-top: 0;

        .section-header-wrapper {
            // padding-top: 4rem;
            // padding-bottom: 4rem;
            align-self: stretch;
            // background: #1d3358;
            // background: linear-gradient(140deg, #2469e0 0%, #1d3359 40%);
            color: #444 !important;

            .section-title1 {
                color: #444 !important;
            }
        }

        ._first-col {
            width: 50%;
        }

        ._second-col {
            width: 50%;
        }

        ._first-col,
        ._second-col {
            display: flex;
            flex-direction: column;

            &>img {
                align-self: center;
            }
        }

        .card {
            padding-left: 2rem;
            padding-right: 2rem;
        }
    }

    &.lottie-section {
        position: relative;
        margin-top: -1px;
        padding-top: 4rem;
        padding-bottom: 8rem;

        // $gradientColor: hsl(163, 100%, 86%);
        $gradientColor: hsl(163, 100%, 86%);
        // background: linear-gradient(90deg, hsl(210, 0%, 100%) 0%, hsl(210, 0%, 95%) 50%, hsl(210, 0%, 100%) 100%);
        // background: linear-gradient(90deg, hsl(210, 0%, 100%) 0%, $gradientColor 50%, hsl(210, 0%, 100%) 100%);
        // background: linear-gradient(90deg, hsl(162, 100%, 86%) 0%, hsl(162, 57%, 61%) 50%, hsl(162, 100%, 86%) 100%);

        background: linear-gradient(180deg, hsl(162, 57%, 61%) 0%, hsl(162, 57%, 50%) 20%, hsl(162, 57%, 50%) 30%, hsl(162, 57%, 61%) 50%, hsl(210, 0%, 100%) 90%);

        // linear-gradient(106deg, #baffea 0%, #63d4b2 50%, #baffea 100%)

        .lottie-section-content {
            max-width: calc(min(58em, 90%));

            .section-image {
                transform: translate(-1.5em, 0);
            }

            ._arrow1,
            ._arrow2 {
                opacity: 0;
            }

            ._arrow1 {
                position: absolute;
                top: 80%;
                right: 100%;
                height: 5rem;
                color: #2b5192;
                // color: hsl(39, 100%, 50%);
            }

            ._arrow2 {
                position: absolute;
                top: 80%;
                left: 100%;
                height: 5rem;
                transform: scale(-1, 1);
                color: hsl(180, 62%, 39%);
            }

            &>._first-col {
                width: 50%;
                transform: translate(0,-1.5rem);

                ul {
                    list-style: none;
                }

                img {
                    filter: drop-shadow(2px 2px 0.25rem rgba(0,0,0,0.75));
                }

                .card {
                    .card-title, .card-text {
                        filter: drop-shadow(0 0 0.5rem #fff);
                    }
                    

                    // background: rgba(255, 255, 255, 0.75);
                    padding: 16px;
                    border-radius: 16px;
                    color: #333;
                }
            }

            &>._second-col {
                width: 50%;                

                ul {
                    list-style: none;
                }

                img {
                    display: none;
                }

                .card {
                    &:not(:first-child) {
                        margin-top: 1rem;
                    }                    
                    background: rgba(255, 255, 255, 0.75);
                    padding: 16px;
                    border-radius: 16px;
                }

            }
        }
    }

    .section-header {
        max-width: calc(min(60em, 90%));

        .section-title2,
        .section-title3 {
            max-width: calc(min(40em, 100%));
            margin-top: 1em;
            margin-bottom: 2em;
        }
    }

    &.icons-section,
    &.splash-section {
    }

    &.icons-section {
        // background: #eee;
        background: linear-gradient(180deg, hsl(221, 100%, 15%) 20%, hsl(221, 100%, 35%) 50%, hsl(221, 100%, 95%) 90%, hsl(221, 100%, 100%) 100%);

        padding-top: 2rem;
        padding-bottom: 8rem;

        .card {

            filter: drop-shadow(0 0 0.5rem #000);

            .card-header .card-title {
                color: #fff;
            }

            .card-text,
            .card-text li {
                color: #fff;
            }
        }

        video {
            padding: 1em;
            box-sizing: border-box;
            width: 30em;
            max-width: 90%;
        }

        // background: #f5f5f7;
        // background: linear-gradient(90deg, hsl(210, 0%, 98%) 0%, hsl(210, 0%, 90%) 50%, hsl(210, 0%, 98%) 100%);
        // background: linear-gradient(90deg, hsl(210, 0%, 100%) 0%, hsl(210, 0%, 95%) 50%, hsl(210, 0%, 100%) 100%);
    }

    &.loaders-section {
        video {
            max-width: calc(min(30em, 90%));
            // padding: 42px;
            border-radius: 16px;
            background: #fff;
            // border: 1px solid rgba(0, 0, 0, 0.1);
            // box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
        }
    }

    &.section-devices {
        margin-top: 4rem;
        margin-bottom: 4rem;
    }

    &.splash-section {
        margin-top: 8rem;
        padding-top: 4rem;
        padding-bottom: 4rem;

        .section-header {
            .section-title1 {
                color: #fff;
            }

            .section-title3 {
                color: #fff;
            }
        }

        .card-header {
            .card-title {
                color: #fff;
            }
        }

        .card-text {
            color: #fff;
        }

        // background: linear-gradient(140deg, #2469e0 0%, #1d3359 40%);
        background: #00194f;
        // background: linear-gradient(90deg, hsl(210, 0%, 98%) 0%, hsl(210, 0%, 90%) 50%, hsl(210, 0%, 98%) 100%);
        // background: linear-gradient(90deg, hsl(210, 0%, 100%) 0%, hsl(210, 0%, 95%) 50%, hsl(210, 0%, 100%) 100%);
    }
}

.pricing-section {
    display: flex;
    flex-direction: column;
    align-items: center;

    margin-top: 64px;
    margin-bottom: 120px;

    .pricing-section-content {
        display: flex;
        flex-direction: column;
        align-items: start;


        .pricing-title {
            margin-top: 3rem;
            margin-bottom: 1rem;
            color: #333;

            ._l1 {
                font-weight: 700;
                font-size: 3rem;
                // line-height: 150% !important;
            }

            ._l2 {
                font-size: 2.5rem;
                font-weight: 400;

                .strong-text {
                    font-weight: 700;
                }

                margin-top: 1rem;
                // line-height: 150% !important;
            }

            ._l3 {
                font-size: 1.5rem;
                font-weight: 400;

                .strong-text {
                    font-weight: 700;
                }

                margin-top: 1.2rem;
                // line-height: 150% !important;
            }
        }

        .pricing-title2 {
            margin-top: 2rem;
            margin-bottom: 1rem;
            font-size: 0.75rem;
        }

        .pricing-features-list {
            margin-top: 3rem;
            margin-bottom: 4rem;
            max-width: 800px;
            font-weight: 300;
            font-size: 1rem;

            li {
                line-height: 200%;
            }
        }
    }
}