@import "vars";


////////////////////////////////////////////////////////////////

.site-titles h1.site-title1 {
    font-size: 3.5rem;
}


.store-buttons {
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}

@media (max-width: $PHONE_MAX_WIDTH) {

    .site-titles h1.site-title1 {
        font-size: 3rem;
    }

    .page1-animations {
        margin-top: 42px;
    }

    .store-buttons {
        flex-direction: column;
        align-items: center;
    }
}


////////////////////////////////////////////////////////////////

.site-titles {
    h1.site-title1 {
        font-weight: 400;
        // color: hsl(218, 54%, 37%);

        .strong-text {
            font-weight: 600;
            // color: hsl(218, 54%, 30%);        
        }
    }


    .site-titles2 h2.site-title2 {
        font-size: 1rem;
        font-weight: 300;
        line-height: 140%;
    }

    .site-titles2 h2.site-title2 .strong-text {
        font-size: 1rem;
        font-weight: 600;
        color: #222;
    }
}

.store-buttons .store-button {
    font-size: 14px;
    color: #fff;
    background: rgb(0, 102, 204);
}

////////////////////////////////////////////////////////////////


.page1 {}

.site-titles {
    z-index: 9;
    text-align: center;
    margin-top: calc(42px + 2em); // needs to be below the header

    margin-left: 1em;
    margin-right: 1em;

    h1.site-title1 {
        text-align: center;
    }

    .site-titles2 {
        margin-top: 1em;
        margin-bottom: 0.5em;

        h2.site-title2 {
            text-align: center;
            max-width: 100%;
        }
    }
}

//Buttons - Stores

.store-buttons {
    display: flex;


    // .store-button-download {
    //     color: hsl(211,100%,30%);
    // }

    a.store-button {
        text-decoration: none;
    }

    .store-button {
        font-size: 0.7rem;
        white-space: nowrap;
        display: inline-block;
        border-radius: 4px;
        height: 32px;
        padding-left: 4px;
        padding-right: 8px;
        cursor: pointer;
        user-select: none;
        margin-left: 8px;
        margin-top: 8px;

        svg {
            height: 24px;
            width: 24px;
            margin-right: 4px;
        }

        &.explore-app-button {
            text-decoration: none;
            font-size: 0.7rem;
            font-family: Montserrat;
            // font-family: Caveat-Bold;
            font-weight: 500;
            font-style: italic;
            color: #0066cc;
            background: rgba(0, 125, 255, 0.1);
            padding-left: 4px;
            padding-right: 8px;
            height: 34px;
            border-radius: 4px;
            white-space: nowrap;            
        }

        min-width: 32px;

        div {
            text-overflow: ellipsis;
            min-width: 0;
            overflow: hidden;
        }
    }
}


.sea-bg {
    margin-top: -4px;
    width: 100%;
    height: 10vw;
    background: #63d4b2;
    // border:1px solid red;
}

////////////////////////////////////////////////////////////////


@media (min-width: 1656px) {
    .page1-animations {
        margin-top: -42px;
    }
}

@media (max-width: 1000px) and (min-width: 700px) {
    .page1-animations {
        margin-top: 16px;
    }
}

@media (max-width: 700px) {
    .page1-animations {
        margin-top: 64px;
    }
}

.page1-animations {
    position: relative;
    width: 100%;
    max-width: 2000px;
    box-sizing: border-box;
    // transform: translate(0,-6%);

    .page1-bg-animation {
        box-sizing: border-box;
        width: 100%;
        padding-top: 48%;
        background-image: url('image/main-boat-bright.jpg');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;

        video {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
        }
    }

    .page1-fg-animation-with-buttons {
        position: relative;
        box-sizing: border-box;
        margin-top: 32px;
        position: absolute;
        top: 37%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 50%;
        padding: 8px 16px;
        background: #222;
        border-radius: 16px;

        .page1-fg-animation-wrap {
            position: relative;
            width: 100%;

            .page1-fg-animation {
                position: relative;
                box-sizing: border-box;
                width: 100%;
                padding-top: 56%; // Keeping video aspect ratio
                background: #fff;
                background-image: url('image/office-small4.png');
                background-size: cover;
                background-repeat: no-repeat;
                background-position: center center;

                video {
                    position: absolute;
                    left: 0;
                    top: 0;
                    width: 100%;
                }
            }
        }
    }
}